<template>
      <div class="login-page">
        <div class="form">
            <form v-if="showRegister" @click.prevent="toggleForm" class="register-form">
                <input type="text" :placeholder="$t('username')"/>
                <input type="password" :placeholder="$t('password')"/>
                <input type="text" :placeholder="$t('emailAddress')"/>
                <button>{{$t('create')}}</button>
                <p class="message">{{$t('alreadyRegistered?')}} <a href="#">{{$t('login')}}</a></p>
            </form>
            <form v-else @click.prevent="toggleForm" class="login-form">
                <input type="text" :placeholder="$t('username')"/>
                <input type="password" :placeholder="$t('password')"/>
                <button>{{$t('login')}}</button>
                <p class="message">{{$t('notRegistered?')}} <a href="#">{{$t('register')}}</a></p>
            </form>
        </div>
    </div>
</template>

<script>
export default {
  name: 'login',
  data(){
    return {
        showRegister : false,
        languageDictionary : {
        login : ["Login", "Anmelden"]
      }
    }
  },
  methods : {
      toggleForm: function() {
          this.showRegister = !this.showRegister;
      }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style>
.login-page {
    width: 360px;
    padding: 10% 0 0;
    margin: auto;
}

.form {
    position: relative;
    z-index: 1;
    background-color: rgba(255, 255, 255, 0.6);
    max-width: 360px;
    margin: 0 auto 100px;
    padding: 45px;
    border-radius: 15px;
    text-align: center;
}

.form input {
    font-family: "Roboto", sans-serif;
    outline: 1;
    background: #f2f2f2;
    width: 100%;
    margin: 0 0 15px;
    padding: 15px;
    box-sizing: border-box;
    font-size: 14px;
}

.form button {
    font-family: "Roboto", sans-serif;
    text-transform: uppercase;
    outline: 0;
    background: cadetblue;
    width: 100%;
    border: 0;
    padding: 15px;
    color: #FFFFFF;
    font-size: 14px;
    cursor: pointer;
}

.form button:hover,.form button:active{
    background: linear-gradient(rgba(0,0,0,0.3),rgba(0,0,0,0.3)),cadetblue;
}

.form .message {
    margin: 15px 0 0;
    font-size: 12px;
}

.form .message a {
    color: cadetblue;
    text-decoration: none;
}
</style>
